import '../../Static.css';
import { Close } from '../Icon/Icon';
import './Menu.css';
import { Link } from 'react-scroll';
import { useTranslation } from 'react-i18next';

export default function Menu(props){
    const { t } = useTranslation();
    return(
        <div className={props.Open ? 'MenuCont Menu--Open' : 'MenuCont'}>
            <div className='MenuContInside'>
                <div className='MenuTitle'>
                    <div>{t("Menu.menu")}</div>
                    <div onClick={()=>props.setOpen(false)}><Close/></div>
                </div>
                <div className='MenuButton'>
                    <Link to='Establishments' spy={true} smooth={true} duration={500}>{t("Menu.est")}</Link>
                    <Link to='Events' spy={true} smooth={true} duration={500}>{t("Menu.event")}</Link>
                    <Link to='Gallery' spy={true} smooth={true} duration={500}>{t("Menu.gallery")}</Link>
                    <Link to='Comments' spy={true} smooth={true} duration={500}>{t("Menu.comment")}</Link>
                    <Link to='Contacts' spy={true} smooth={true} duration={500}>{t("Menu.contact")}</Link>
                </div>
            </div>
        </div>
    )
}