import '../../StaticElement/Static.css';
import './Establishments.css';
import {useNavigate} from 'react-router-dom';

export default function EstablishmentsCard(props){
    const navigate = useNavigate();
    return(
        <div className='EstablishmentsCardCont'>
            <div className='EstablishmentsCardPhotoCont' onClick={()=> navigate(`/${props.href}`)}>
                <img src={props.img} alt='Room' className='PhotoRoom'/>
                <img src={props.logo} alt='logo' className='EstablishmentsLogo'/>
            </div>
            <div className='EstablishmentsCardTitle'>{props.title}</div>
            <div className='EstablishmentsCardInfoCont'>
                <div className='EstablishmentsCardInfo'><strong>Адрес:</strong> {props.adres}</div>
                <div className='EstablishmentsCardInfo'><strong>E-mail:</strong> <a href={'mailto:' + props.mail}>{props.mail}</a></div>
                <div className='EstablishmentsCardInfo'><strong>Время работы:</strong> {props.time}</div>
            </div>
        </div>
    )
}