import './RestComment.css';
import '../../StaticElement/Static.css';
import { useRef, useState, useEffect } from 'react';
import RestCommentRating from './RestCommentRating';

export default function RestCommentModal(props){
    const [Kitchen, setKitchen] = useState(0);
    const [Comfort, setComfort] = useState(0);
    const [Service, setService] = useState(0);
    const [Comment, setComment] = useState({Name:'', Phone:'', Mail:'', Comment:''});
    const CommentModal = useRef();

    function ComModal(e){
        if(e.target.className.indexOf('RestCommentModalCont') !== -1){
            document.body.style.overflow = 'auto';           
            CommentModal.current.classList.toggle('RestCommentModalCont--Active');
            props.setDisplay(false);
            setComment({Name:'', Phone:'', Mail:'', Comment:''});
            setKitchen(0);
            setComfort(0);
            setService(0);
        }
    }

    useEffect(() => {
        if(props.display === true){
            document.body.style.overflow = 'hidden';            
            CommentModal.current.classList.toggle('RestCommentModalCont--Active');
        }
    }, [props.display]);
    
    function SendComment(e){
        e.preventDefault();
        let SendComment = {...Comment, Kitchen:Kitchen, Comfort:Comfort, Service:Service};
        console.log(SendComment);
    }

    return(
        <div ref={CommentModal} className='RestCommentModalCont' onClick={ComModal}>
            <div className='RestCommentModal'>
                <div className='RestCommentTitle RestCommentTitleModal'>
                    <div>ОСТАВИТЬ ОТЗЫВ</div>
                    <hr color='Black' width='30%' style={{border:'0.01px black solid', margin:'0.5vw 0px'}}/>
                </div>
                <div className='RestCommentDiscModal'>На этой странице Вы можете ознакомиться с отзывами наших клиентов, а также оставить свой отзыв о работе ресторана {props.Name}.<br/>Нам очень важно Ваше мнение!</div>
                <form name='CommentForm'>
                    <input required type='text' placeholder='Имя*' value={Comment.Name} onChange={(e) => setComment({...Comment, Name:e.target.value})}/>
                    <input required type='text' placeholder='Телефон*' value={Comment.Phone} onChange={(e) => setComment({...Comment, Phone:e.target.value})}/>
                    <input required type='email' placeholder='E-Mail*' value={Comment.Mail} onChange={(e) => setComment({...Comment, Mail:e.target.value})}/>
                    <textarea required placeholder='Текст отзыва*' value={Comment.Comment} rows={5} onChange={(e) => setComment({...Comment, Comment:e.target.value})}/>
                    <div className='RestCommentCardRatingElCont RestCommentCardRatingElContModal'>
                        <div>Кухня</div>
                        <RestCommentRating Rating={Kitchen} setRating={setKitchen} display={props.display}/>
                    </div>
                    <div className='RestCommentCardRatingElCont RestCommentCardRatingElContModal'>
                        <div>Конфорт</div>
                        <RestCommentRating Rating={Comfort} setRating={setComfort} display={props.display}/>
                    </div>
                    <div className='RestCommentCardRatingElCont RestCommentCardRatingElContModal'>
                        <div>Обслуживания</div>
                        <RestCommentRating Rating={Service} setRating={setService} display={props.display}/>
                    </div>
                    <button className='RestMenuShowButton' onClick={SendComment}>Отправить отзыв</button>
                </form>
            </div>
        </div>
    )
}