import './RestComment.css';
import '../../StaticElement/Static.css';
import { CurvTwo, Star } from '../../StaticElement/Component/Icon/Icon';

export default function RestCommentCard(props){
    let kitchenStar = [];
    for(let i = 0; i < props.kitchen; i++) kitchenStar.push(i + 1);
    let comfortStar = [];
    for(let i = 0; i < props.comfort; i++) comfortStar.push(i + 1);
    let serviceStar = [];
    for(let i = 0; i < props.service; i++) serviceStar.push(i + 1);

    return(
        <div className={props.className}>
            <div className='RestCommentCardTitle'>
                <CurvTwo/>
                {props.name}
            </div>
            <div className='RestCommentCardBody'>
                <div>{props.text}</div>
                <div className='RestCommentCardBodyRatingCont'>
                    <div className='RestCommentCardRatingElCont'>
                        <div>Кухня</div>
                        <div className='RestCommentCardRatingStar'>{kitchenStar.map((item, i) => <Star key={i}/>)}</div>
                    </div>
                    <div className='RestCommentCardRatingElCont'>
                        <div>Комфорт</div>
                        <div className='RestCommentCardRatingStar'>{comfortStar.map((item, i) => <Star key={i}/>)}</div>
                    </div>
                    <div className='RestCommentCardRatingElCont'>
                        <div>Обслуживание</div>
                        <div className='RestCommentCardRatingStar'>{serviceStar.map((item, i) => <Star key={i}/>)}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}