import './RestContact.css';
import '../../StaticElement/Static.css';

export default function RestContact(props){
    return(
        <div className='RestContactCont' id='Contacts'>
            <div className='RestContact'>
                <div className='RestContactTitle'>Местонахождение</div>
                <div className='RestContactText'>{props.ContactLoc}</div>
                <div className='RestContactTitle'>Время работы</div>
                <div className='RestContactText'>{props.ContactTime}</div>
                <div className='RestContactTitle'>Контакты</div>
                <div className='RestContactText'>Email: {props.ContactMail}</div>
                <div className='RestContactText'>Телефон: {props.ContactTel}</div>
                <div className='RestContactBron'>Забронировать столик</div>
            </div>
            <iframe src={props.ContactMap} className='RestContactMap' frameBorder="0" title='Map'></iframe>
        </div>
    )
}