import './RestHeader.css';
import '../../StaticElement/Static.css';
import { useRef, useState, useEffect } from 'react';
import RestTrashCardModal from './RestTrashCardModal';

export default function RestTrashModal(props){
    const [Trash, setTrash] = useState({Name:'', Phone:''});
    const TrashModal = useRef();

    function ComModal(e){
        console.log(typeof(e.target));
        if(e.target.className.indexOf('RestCommentModalCont') !== -1){
            document.body.style.overflow = 'auto';           
            TrashModal.current.classList.toggle('RestCommentModalCont--Active');
            props.setDisplay(false);
            setTrash({Name:'', Phone:''});
        }
    }

    useEffect(() => {
        if(props.display === true){
            document.body.style.overflow = 'hidden';            
            TrashModal.current.classList.toggle('RestCommentModalCont--Active');
        }
    }, [props.display]);
    
    function SendComment(e){
        e.preventDefault();
        console.log(Trash);
    }

    return(
        <div ref={TrashModal} className='RestCommentModalCont' onClick={ComModal}>
            <div className='RestCommentModal RestTrashModal'>
                <div className='RestCommentTitle RestCommentTitleModal'>
                    <div>{props.Name.toUpperCase()} RESTAURANT</div>
                    <hr color='Black' width='30%' style={{border:'0.01px black solid', margin:'0.5vw 0px'}}/>
                </div>
                <div className='RestTrashEl'>
                    <RestTrashCardModal Title='Террин из кролика с фисташками (205g)' id='26'/>
                </div>
                <form name='CommentForm'>
                    <div style={{display:'flex', flexDirection:'column', alignItems:'center', justifyContent:'space-between', width:'60%', height:'45%'}}>
                        <input required type='text' placeholder='Имя*' value={Trash.Name} onChange={(e) => setTrash({...Trash, Name:e.target.value})}/>
                        <input required type='text' placeholder='Телефон*' value={Trash.Phone} onChange={(e) => setTrash({...Trash, Phone:e.target.value})}/>
                    </div>
                    <div style={{display:'flex', flexDirection:'column', alignItems:'top', width:'40%', height:'45%'}}>
                        <input required type='text' placeholder='Время*' value={Trash.Name} onChange={(e) => setTrash({...Trash, Name:e.target.value})}/>
                    </div>
                </form>
                    <button className='RestMenuShowButton' onClick={SendComment}>Оформить бронирование</button>
            </div>
        </div>
    )
}