import './RestHeader.css';
import '../../StaticElement/Static.css';
import { useState } from 'react';
import { Cansel, Minus, Plus } from '../../StaticElement/Component/Icon/Icon';

export default function RestTrashCardModal(props){
    const [Count, setCount] = useState(1);
    return(
        <div className='RestTrashCardCont'>
            <div className='RestTrashCardTitle'>{props.Title}</div>
            <div className='RestTrashCardCount'><Minus onClick={(e) => {if(Count !== 0)setCount(Count-1)}}/><div>{Count}</div><Plus onClick={(e) => setCount(C => C + 1)}/></div>
            <div className='RestTrashCardCount'>{props.id}</div>
            <Cansel/>
        </div>
    )
}