import './3D.css';
import '../../StaticElement/Static.css';
import { ArrowButtonStandard } from '../../StaticElement/Component/Icon/Icon';
import { useRef } from 'react';

export default function RestTour(){
    let Tour = useRef(),
        Shema = useRef();
    function Slide(){
        Tour.current.classList.toggle('RestTour--Active');
        Shema.current.classList.toggle('RestSchema--Active');
    }
    return(
        <div className='RestTour' id='3D'>
            <div ref={Tour} className='RestTourCont'>
                <div className='RestTourBlockTwo'>
                    <div className='RestTourBlockTwoTitle'>
                        <div>3D ТУР</div>
                        <hr color='Black' width='30%' style={{margin:'0px', border:'0.01px black solid'}}/>
                    </div>
                    <div className='RestTourBlockTwoBody'>Для бронирования столика следует тщательно выбрать себе место, но как выбрать место, не находясь в ресторане? <br/> Легко! Мы сделали для вас 3D тур</div>
                    <div className='RestMenuShowButton'>Посмотреть меню</div>
                </div>
                <img src={require('../../StaticElement/Image/3D.png')} alt='3DTour' className='TourPhoto'/>
                <ArrowButtonStandard onClick={Slide} id='arr4' rotate='0'/>
            </div>
            <div ref={Shema} className='RestSchemaCont'>
                <ArrowButtonStandard onClick={Slide} id='arr5' rotate='1'/>
                <div className='RestTourBlockTwo'>
                    <div className='RestTourBlockTwoTitle'>
                        <div>Схема ресторана</div>
                        <hr color='Black' width='30%' style={{margin:'0px', border:'0.01px black solid'}}/>
                    </div>
                    <div className='RestTourBlockTwoBody'>Схема ресторана поможеть выбрать ???</div>
                    <div className='RestMenuShowButton'>Открыть схему</div>
                </div>
                <img src={require('../../StaticElement/Image/3D.png')} alt='3DTour' className='TourPhoto'/>
            </div>
        </div>
    )
}