import './Carousel.css';
import { useState } from "react";
import EventsCard from '../../../Home/Events/EventsCard';
import { ArrowButtonStandard } from '../Icon/Icon';
import RestCommentCard from '../../../RestTemplate/RestComment/RestCommentCard';
export default function Carousel(props){
    const [index, setIndex] = useState(0);
    

    function SlideLeft(){
        setIndex(((index - 1) % props.cards.length) < 0 ? props.cards.length - 1 : ((index - 1) % props.cards.length))
    }

    function SlideRight(){
        setIndex((index + 1) % props.cards.length)
    }

    const mod = (n, m) => {
        let result = n % m;
        return result >= 0 ? result : result + m;
    };
    

    return(
        <div className="CaruselCont">
            <div className="Carousel">
                {props.cards.map((item, i) => {
                    const indexLeft = mod(index - 1, props.cards.length);
                    const indexRight = mod(index + 1, props.cards.length);
                    let className;
                    if(props.sw === 1) className = 'card';
                    else if(props.sw === 2) className = 'Restcard';
                    if (i === index) {
                        className += " card--active";
                    } else if (i === indexRight) {
                        className += " card--right";
                    } else if (i === indexLeft) {
                        className += " card--left";
                    }

                    if(props.sw === 1)
                        return (
                            <EventsCard key={item.id} className={className} img={item.img} restaurant={item.restaurant} date={item.date} title={item.title} text={item.text}/>
                        );
                    else if(props.sw === 2)
                        return (
                            <RestCommentCard key={item.id} className={className} name={item.name} text={item.text} kitchen={item.kitchen} comfort={item.comfort} service={item.service}/>
                        );
                    else return 0;
                })}
            </div>
            <div className='Arrows'>
                <ArrowButtonStandard onClick={SlideLeft} id='arr1' rotate='1'/>
                <ArrowButtonStandard onClick={SlideRight} id='arr2' rotate='0'/>
            </div>
        </div>
    )
}