import './Header.css';
import '../../StaticElement/Static.css';
import CustomSelect from '../../StaticElement/Component/CustomSelect/CustomSelect';
import { useRef, useEffect, useState } from 'react';
import Scroll from 'react-scroll';
import PhotoSliderButtons from './PhotoSliderButtons';
import { NRGlogo, MenuButton, Telegram, Instagram, Mail, ArrowButton } from '../../StaticElement/Component/Icon/Icon';
import Menu from '../../StaticElement/Component/Menu/Menu';
import { useTranslation } from 'react-i18next';

export default function Home(){
    const [Stage, setStage] = useState(2);
    const [MenuOpen, setMenuOpen] = useState(false);
    let Z1 = useRef(),
        Z2 = useRef(),
        Z3 = useRef(),
        Z4 = useRef();
    const [ZActive, setZActive] = useState(Z1);
    const { t } = useTranslation();
    function Slider(st){
        switch(st){
            case 1:
                ZActive.current.classList.toggle('Z--Active');
                setZActive(Z1);
                break;
            case 2:
                ZActive.current.classList.toggle('Z--Active');
                Z2.current.classList.toggle('Z--Active');
                setZActive(Z2);
                break;
            case 3:
                ZActive.current.classList.toggle('Z--Active');
                Z3.current.classList.toggle('Z--Active');
                setZActive(Z3);
                break;
            case 4:
                ZActive.current.classList.toggle('Z--Active');
                Z4.current.classList.toggle('Z--Active')
                setZActive(Z4);
                break;
            default:
                break;
        }
    }
    useEffect(() => {
        let interval = setInterval(()=>{
            if(Stage + 1 > 4) setStage(1);
            else setStage(S=>S + 1);
            Slider(Stage);
        }, 5000)
        return () => clearInterval(interval);
    });
    return(
        <div className='HomeCont'>
            <Menu  Open={MenuOpen} setOpen={setMenuOpen}/>
            <div className='StartWindow'>
                <img className='Z1' ref={Z1} src={require('../../StaticElement/Image/1.webp')} alt='Back'/>
                <img className='Z2' ref={Z2} src={require('../../StaticElement/Image/2.webp')} alt='Back'/>
                <img className='Z3' ref={Z3} src={require('../../StaticElement/Image/3.webp')} alt='Back'/>
                <img className='Z4' ref={Z4} src={require('../../StaticElement/Image/4.webp')} alt='Back'/>
                <table style={{width:'100vw', border:'0px', zIndex:'3'}}>
                    <tbody>
                        <tr className='GridLineOne'>
                            <td className='td--noTopLine td--extreme'>
                                <CustomSelect/>
                            </td>
                            <td className='td--noTopLine Logo1'>
                                <NRGlogo/>
                            </td>
                            <td className='td--noRightLine td--extreme Menu'>
                                <div onClick={() => setMenuOpen(true)}><div className='MenuButtonText'>{t("Menu.menu")}</div><MenuButton/></div>
                            </td>
                        </tr>
                        <tr className='GridLineTwo'>
                            <td className='td--extreme td--noBottomLine'>
                                <PhotoSliderButtons Stage={Stage} setStage={setStage} Slider={Slider}/>
                            </td>
                            <td className='td--noBottomLine'>
                                <div className='HomeLogo'>
                                    <img src={require('../../StaticElement/Image/Logo.png')} alt='logo'/>
                                    <Scroll.Link to='Establishments' spy={true} smooth={true} duration={500}><ArrowButton/></Scroll.Link>
                                </div>
                            </td>
                            <td className='td--noRightLine td--extreme td--noBottomLine'>
                                <div className='SocialCont'>
                                    <Telegram href='/'/>
                                    <Instagram href='/'/>
                                    <Mail href='/'/>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    )
}