import './Gallery.css';
import '../../StaticElement/Static.css';

export default function PhotoCard(props){
    return(
        <div className='PhotoCardCont' style={{width:props.width, height:props.height}}>
            <div className='PhotoCardTextCont'>
                <div className='PhotoCardTextTitle'>{props.title}</div>
                <div className='PhotoCardText'>{props.text}</div>
            </div>
            <div className='PhotoCardPhoto' style={{background:`url(${props.img})`, backgroundPosition: 'center', backgroundRepeat: 'no-repeat', backgroundSize: 'cover', width:'100%', height:'100%'}}/>
        </div>
    )
}