import Comments from './Comments/Comments';
import Contacts from './Contacts/Contacts';
import Establishments from './Establishments/Establishments';
import Events from './Events/Events';
import Footer from './Footer/Footer';
import Gallery from './Gallery/Gallery';
import Header from './Header/Header';
import { useTranslation } from 'react-i18next';
export default function Home(){
    const { t } = useTranslation();
    return(
        <div>
            <Header/>
            <div style={{background:`url(${require('../StaticElement/Image/HomeBack.png')})`, backgroundPosition:'top', backgroundRepeat:'no-repeat', backgroundSize:'contain'}}>
                <Establishments/>
                <Events/>
                <Gallery/>
            </div>
            <Comments/>
            <Contacts/>
            <Footer links={[{link:'Establishments', text:t("Menu.est")}, {link:'Events', text:t("Menu.event")}, {link:'Gallery', text:t("Menu.gallery")}, {link:'Comments', text:t("Menu.comment")}, {link:'Contacts', text:t("Menu.contact")}]}/>
        </div>
    )
}