import './Header.css';
import { useRef, useEffect, useState } from 'react';

export default function PhotoSliderButtons(props){
    let Z1Button = useRef(),
        Z2Button = useRef(),
        Z3Button = useRef(),
        Z4Button = useRef();
    const [ZActiveButton, setZActiveButton] = useState(Z1Button);

    useEffect(() => {
        let interval = setInterval(()=>{
            Move(props.Stage);
        }, 5000)
        return () => clearInterval(interval);
    });

    function Move(st){
        switch(st){
            case 1:
                ZActiveButton.current.classList.toggle('CircleButton--Active');
                Z1Button.current.classList.toggle('CircleButton--Active');
                setZActiveButton(Z1Button);
                break;
            case 2:
                ZActiveButton.current.classList.toggle('CircleButton--Active');
                Z2Button.current.classList.toggle('CircleButton--Active');
                setZActiveButton(Z2Button);
                break;
            case 3:
                ZActiveButton.current.classList.toggle('CircleButton--Active');
                Z3Button.current.classList.toggle('CircleButton--Active');
                setZActiveButton(Z3Button);
                break;
            case 4:
                ZActiveButton.current.classList.toggle('CircleButton--Active');
                Z4Button.current.classList.toggle('CircleButton--Active')
                setZActiveButton(Z4Button);
                break;
            default:
                break;
        }
    }

    function Change(e){
        ZActiveButton.current.classList.toggle('CircleButton--Active');
        e.target.classList.toggle('CircleButton--Active');
        switch (parseInt(e.target.getAttribute('value'))) {
            case 1:
                setZActiveButton(Z1Button);
                break;
            case 2:
                setZActiveButton(Z2Button);
                break;
            case 3:
                setZActiveButton(Z3Button);
                break;
            case 4:
                setZActiveButton(Z4Button);
                break;
            default:
                break;
        }
        props.setStage(parseInt(e.target.getAttribute('value')));
        props.Slider(parseInt(e.target.getAttribute('value')));
    }

    return(
        <div className='CircleButtonCont'>
            <div ref={Z1Button} className='CircleButton CircleButton--Active' value='1' onClick={Change}/>
            <div ref={Z2Button} className='CircleButton' value='2' onClick={Change}/>
            <div ref={Z3Button} className='CircleButton' value='3' onClick={Change}/>
            <div ref={Z4Button} className='CircleButton' value='4' onClick={Change}/>
        </div>
    )
}