import './Comments.css';
import '../../StaticElement/Static.css';
import { Curv } from '../../StaticElement/Component/Icon/Icon';

export default function Comment(props){
    return(
        <div className='CommentCont' onMouseEnter={(e)=> e.target.classList.toggle('CommentCont--Active')} onMouseLeave={(e)=> e.target.classList.toggle('CommentCont--Active')}>
            <div className='CommentHeaderCont'>
                <div className='CommentHeaderContInside'>
                    <div className='CommentHeaderTextOne'>{props.name}</div>
                    <div className='CommentHeaderTextTwo'>{props.city}</div>
                </div>
                <Curv/>
            </div>
            <hr style={{width:'90%'}}/>
            <div className='CommentText'>{props.comment}</div>
        </div>
    )
}