import './RestHeader.css';
import '../../StaticElement/Static.css';
import { Link } from 'react-scroll';
import CustomSelect from '../../StaticElement/Component/CustomSelect/CustomSelect';
import { Instagram, Trash } from '../../StaticElement/Component/Icon/Icon';
import { useState, useRef, useEffect } from 'react';
import RestPhotoSliderButtons from './RestPhotoSliderButtons';
import RestTrashModal from './RestTrashModal';

export default function RestHeader(props){
    const [Stage, setStage] = useState(2);
    const [Display, setDisplay] = useState(false);
    let Z1 = useRef(),
        Z2 = useRef(),
        Z3 = useRef();
    const [ZActive, setZActive] = useState(Z1);
    function Slider(st){
        switch(st){
            case 1:
                ZActive.current.classList.toggle('Z--Active');
                setZActive(Z1);
                break;
            case 2:
                ZActive.current.classList.toggle('Z--Active');
                Z2.current.classList.toggle('Z--Active');
                setZActive(Z2);
                break;
            case 3:
                ZActive.current.classList.toggle('Z--Active');
                Z3.current.classList.toggle('Z--Active')
                setZActive(Z3);
                break;
            default:
                break;
        }
    }
    useEffect(() => {
        let interval = setInterval(()=>{
            if(Stage + 1 > 3) setStage(1);
            else setStage(S=>S + 1);
            Slider(Stage);
        }, 5000)
        return () => clearInterval(interval);
    });
    return(
        <div className='RestHeaderCont'>
        <RestTrashModal display={Display} setDisplay={setDisplay} Name={props.Name}/>
            <img className='Z1' ref={Z1} src={props.HeaderImgArr[0]} alt='Back'/>
            <img className='Z2' ref={Z2} src={props.HeaderImgArr[1]} alt='Back'/>
            <img className='Z3' ref={Z3} src={props.HeaderImgArr[2]} alt='Back'/>
            <div className='RestNavCont'>
                <div className='RestNavButtonCont'>
                    <img src={props.Logo} alt='Logo'/>
                    <Link to='About' spy={true} smooth={true} duration={500}>О нас</Link>
                    <Link to='Menu' spy={true} smooth={true} duration={500}>Меню</Link>
                    <Link to='Gallery' spy={true} smooth={true} duration={500}>Галерея</Link>
                    <Link to='Comment' spy={true} smooth={true} duration={500}>Отзывы</Link>
                    <Link to='3D' spy={true} smooth={true} duration={500}>3D Тур</Link>
                    <Link to='Contacts' spy={true} smooth={true} duration={500}>Контакты</Link>
                </div>
                <div className='RestNavButtonContTwo'>
                    <CustomSelect/>
                    <Instagram/>
                    <div className='RestMenuButton'>Меню</div>
                </div>
            </div>
            <div className='RestHeaderSliderCont'>
                {/* <img src={require('../../StaticElement/Image/TheView/ArrowLeft.png')} alt='Left'/> */}
                <div className='RestHeaderTitleCont'>
                    <div className='RestHeaderTitleOne'>{props.Title}</div>
                    <div className='RestMenuButton'>Посмотреть 3D тур</div>
                    <div className='RestHeaderBron'>Забронировать <div className='RestArrow'/></div>
                </div>
                {/* <img src={require('../../StaticElement/Image/TheView/ArrowRight.png')} alt='Right' onClick={()=>{}/> */}
            </div>
            <div className='RestHeaderFooterCont'>
                <div className='RestInvBox'/>
                <RestPhotoSliderButtons Slider={Slider} setStage={setStage} Stage={Stage}/>
                <div className='RestHeaderFooterTrash' onClick={() => setDisplay(true)}><div className='RestTrashTitle'>Корзина</div><div className='RestTrash'><Trash/></div></div>
            </div>
        </div>
    )
}