import '../../StaticElement/Static.css';
import './Events.css';

export default function EventsCard(props){
    // var arrMonth=[
    //        'Января',
    //        'Февраля',
    //        'Марта',
    //        'Апреля',
    //        'Мая',
    //        'Июня',
    //        'Июля',
    //        'Августа',
    //        'Сентября',
    //        'Ноября',
    //        'Декабря',
    //     ];
        
    return(
        <div className={props.className}>
            <div className='EventsCardButton'>Подробнее</div>
            <div className='EventsCardPhoto' style={{background:`url(${props.img})`}}/>
            <div className='EventsCardBody'>
                <div className='EventsCardTextCont'>
                    <div className='EventsCardText'>{props.restaurant}</div>
                    <div className='EventsCardText'>{new Date(props.date).getDate() < 10 ? '0' + new Date(props.date).getDate() : new Date(props.date).getDate()}.{new Date(props.date).getMonth() < 10 ? '0' + new Date(props.date).getMonth() : new Date(props.date).getMonth()}.{new Date(props.date).getFullYear()}</div>
                </div>
                <div className='EventsCardTextContTwo'>
                    <div className='EventsCardTextContTwoTitle'>{props.title}</div>
                    <div className='EventsCardTextContTwoText'>{props.text}</div>
                </div>
            </div>
        </div>
    )
}