import { useRef } from 'react';
import './CustomSelect.css';
import '../../Static.css';
import { useTranslation } from "react-i18next";

export default function CustomSelect(){
    const { i18n } = useTranslation();
    let select__current = useRef();
    let select = useRef()
    function selectChoose(e) {
        let text = e.target.innerText,
            select = e.target.closest('.select'),
            currentText = select__current.current;
        currentText.innerText = text;
        i18n.changeLanguage(text.toLowerCase());
        select.classList.remove('is-active');
    }

    return(
        <div ref={select} className="select">
            <label onClick={() => select.current.classList.toggle('is-active')}>
                <div className="select__header" >
                    <div ref={select__current} className="select__current">RU</div>
                </div>
            </label>
            <div className="select__body">
                <div className="select__item" onClick={selectChoose}>RU</div>
                <div className="select__item" onClick={selectChoose}>EN</div>
            </div>
        </div>
    )
}